var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("猫女")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        猫女是由Bill Finger和Bob Kane创作的虚构人物，他出现在由DC Comics出版的美国漫画书中，通常与超级英雄蝙蝠侠合作。这个角色在蝙蝠侠＃1（1940年6月）首次亮相为“猫” ，她的真名是Selina Kyle。她是蝙蝠侠最持久的爱情，以与她复杂的爱恨交织而闻名。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        猫女是高谭城的一名窃贼，通常穿着紧身的单件装，并使用牛鞭作为武器。她最初被描述为蝙蝠侠的超级恶魔和对手，但自20世纪90年代以来，她一直被列入一系列故事中，将她描绘成一个反潜机，经常以正确的理由做错事。这个角色自她最早出现以来一直蓬勃发展，但由于1954年开发的漫画规则管理局，她从1954年9月到1966年11月延长了中断。这些问题涉及违反漫画的女性角色的发展和描绘规则。代码，不再使用的代码。在漫画中，霍莉罗宾逊除了Selina Kyle之外，Eiko Hasigawa都采用了猫女身份。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        蝙蝠侠的共同创作者鲍勃凯恩是一位伟大的电影迷，他对电影的热爱为几位蝙蝠侠角色提供了动力，其中包括猫女。她受到20世纪30年代电影明星让·哈洛（Jean Harlow）的部分启发，他在凯恩当时的早期和“易受影响的时代......似乎在其最感性的时候将女性化的风格化为人格化”。凯恩和手指想要给他们的漫画书性感吸引力，以及一个可以吸引女性读者的角色; 因此，他们创造了一个“犯罪的友好敌人，但也对蝙蝠侠相当无聊的生活充满了浪漫的兴趣”。猫女本来是一个爱的兴趣，并参与蝙蝠侠的国际象棋比赛，他试图改革她。与此同时，这个角色与其他蝙蝠侠恶棍（如小丑）不同，因为她从不是杀手或邪恶。Selina Kyle第一次出现在蝙蝠侠＃1中的猫（1940年春）\n这个角色也部分受到了凯恩的堂兄露丝·斯蒂尔的启发。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        猫女，当时被称为“猫”，最初出现在蝙蝠侠＃1（1940年春），作为一个神秘的窃贼和珠宝小偷，在故事的结尾透露是一个年轻，有吸引力（未命名）的女人，伪装在故事中她自己是一位老妇人，并被雇用进行入室盗窃。虽然她没有穿着她标志性的猫套装，但这个故事确立了她作为女性美女的核心个性，她既对抗又吸引了蝙蝠侠。暗示蝙蝠侠可能故意让她在阻止罗宾试图跳过她时逃脱。她接下来出现在蝙蝠侠＃2的故事中也涉及小丑，但最终逃脱蝙蝠侠。在蝙蝠侠 ＃3她戴着毛皮面具，再次成功逃脱蝙蝠侠。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","hei